+component(shop-blocks)
  

  +part(main)

    +option(content)
      color: $color-white

      img
        margin-top: 2em !important
        +avatar
        max-width: 100px
        background: $color-white

      h3
        margin-top: 1.5em
        color: $color-white

    +option(author)
      font-size: 12px
      margin-top: -1em
      text-transform: uppercase

    +option(category)
      padding: 2em

      border: 2px white solid
      background-color: $color-green
      min-height: 200px

      img
        height: 95px
        +white-image

      color: $color-white
      h2
        color: $color-white
        text-transform: uppercase
        font-family: inherit

    +option(shop)

      border: 2px white solid
      background-color: $color-grey

      background-position: center center
      background-size: cover

      min-height: 200px

  +part(secondary)

    +option(shop)
      border: 2px white solid
      background-color: $color-light-green
      min-height: 200px

      background-position: center center
      background-size: cover
      background-image: url(https://images.unsplash.com/photo-1446034730750-a0b64d06ad13?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&s=cfec14f7b7a5269458ce76d08cb7696d)
